body {
    font-size: 18px;
}

.container {
    max-width: 800px;
}

header {
    margin-top: 20px;
    margin-bottom: 15px;
}

header nav.navbar-default {
    background: url("/img/saturn.jpg") center center no-repeat;
    background-size: cover;
    border-radius: 10px;
    border: none;
}

header nav.navbar-default a {
    color: #ffffff !important;
}

header nav.navbar-default a.navbar-brand img {
    height: 24px;
}

header nav.navbar-default input.search {
    background: transparent;
    color: #ffffff;
}

article {
    color: #444;
    margin-bottom: 60px;
    clear: both;
}

article .meta {
    margin-bottom: 20px;
    color: #999999;
    font-size: 14px;
}

article .comments {
    margin-top: 60px;
    margin-bottom: 60px;
}

article .more {
    margin-top: 20px;
    text-align: left;
}

article img {
    max-width: 100%;
    height: auto;
}

footer {
    margin-top: 60px;
    margin-bottom: 60px;
    color: #535353;
    font-size: 14px;
}

/**********************
 *    Extra styles    *
 **********************/

del {
    color: #b60000;
}

img.center {
    display: block !important;
    margin-left: auto;
    margin-right: auto;
}

img.left {
    float: left;
    margin-right: 15px;
}

img.right {
    float: right;
    margin-left: 15px;
}

.info {
    margin-left: 0;
    margin-top: 20px;
    margin-bottom: 20px;
    padding: 20px;
    border-left: 10px solid #5bc0de;
    background-color: #f4f8fa;
}

.gist {
    font-size: 80% !important;
    line-height: normal !important;
}

.gist pre {
    font-size: 12px !important;
}

code {
    color: #333;
    background-color: #f5f5f5;
    border: 1px solid #ccc;
    border-radius: 4px;
}

pre code {
    border: none;
    border-radius: 0;
    background-color: transparent;
}

.hljs {
    padding: 0;
}
